html, body {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #333;
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  scroll-behavior: smooth;
  box-sizing: border-box;
}

.header {
  background: #333;
  color: #fff;
  padding: 20px 0;
  text-align: center;
}

.header h1 {
  font-size: 36px;
  color: #fff;
  margin: 0;
}

.main-content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 50px 20px;
  width: 100%;
  min-height: calc(100vh - 100px); /* 100px este pentru header + footer */
  box-sizing: border-box;
  overflow-y: auto;
  margin-left: 270px; /* Ajustează această valoare dacă vrei să faci mai mult loc pentru taburi */
}

.content {
  width: 80%;
  padding: 20px;
  background: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.tabs {
  display: flex;
  flex-wrap: wrap; /* Permite ca tab-urile să se aranjeze pe mai multe linii, dacă e necesar */
  justify-content: center;
  padding: 20px;
  position: absolute;
  top: 100px;
  left: 0;
  width: 250px;
  box-sizing: border-box;
  max-height: calc(100vh - 100px);
  overflow-y: auto;
  z-index: 10;
}

.tabs button {
  width: 110px;
  height: 110px;
  border: none;
  cursor: pointer;
  margin-bottom: 5px;
  margin-left: auto;
  margin-right: auto;
  transition: transform 0.3s ease;
  border-radius: 50%; /* Asigură că butonul este rotund */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
  transform: scale(1);
}

.tab-image {
  width: 120%; /* Asigură că imaginea ocupă întreaga lățime a butonului */
  height: 120%; /* Asigură că imaginea ocupă întreaga înălțime a butonului */
  object-fit: cover; /* Cea mai bună metodă de a păstra imaginea proporțională */
  border-radius: 50%; /* Aplicăm border-radius pentru a face imaginea rotundă */
}

.tabs button:hover {
  transform: scale(1.1);
}

.tabs button.active {
  color: white;
}

.tabs button::after {
  content: attr(data-text);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: rgb(255, 255, 255);
  font-weight: bold;
  font-size: 14px;
  opacity: 0;
  transition: opacity 1s ease;
  text-shadow: 6px 6px 15px rgba(0, 0, 0, 0.9);
}

.tabs button:hover::after {
  opacity: 1;
}

.tabs button.active::after {
  opacity: 1;
  color: white;
  font-weight: bold;
  text-shadow: 6px 6px 15px rgba(0, 0, 0, 0.9);
}

.social-links a {
  display: inline-block;
  margin-right: 15px;
}

.social-links img {
  margin-right: 10px;
  width: 80px;
  height: 80px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.social-links img:hover {
  transform: scale(1.3);
}

.footer {
  text-align: center;
  padding: 20px 0;
  background: #333;
  color: #fff;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 999;
}

.footer .contact-and-social .contact-list {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
  gap: 30px;
  flex-wrap: wrap; /* Permite ca butoanele să treacă pe o linie următoare dacă nu sunt suficiente spațiu */
  flex-direction: row; /* Asigurăm că butoanele sunt pe orizontală */
}

.footer .contact-and-social .contact-icon {
  width: 40px;
  height: 40px;
  cursor: pointer;
  overflow: hidden;
  transition: transform 0.3s ease;
}

.footer .contact-and-social .contact-icon:hover {
  transform: scale(1.3);
}

/* Media Queries pentru ecrane mici (sub 768px) */
@media (max-width: 768px) {
  .main-content {
    margin-left: 0;
    flex-direction: column;
    padding: 20px;
  }

  .tabs {
    position: static;
    width: 100%;
    max-height: none;
    overflow: visible;
    padding: 10px;
    flex-direction: row; /* Tab-urile rămân pe orizontală */
    justify-content: space-evenly; /* Aliniem tab-urile orizontal, dar cu spațiu între ele */
  }

  .tabs button {
    width: 80px;
    height: 80px;
    margin: 5px; /* Distanțăm mai mult butoanele */
  }

  .footer .contact-and-social .contact-list {
    flex-direction: row; /* Menținem butoanele pe orizontală */
    justify-content: space-evenly; /* Distribuim uniform butoanele pe orizontală */
    gap: 15px; /* Spațiu mai mic între butoane */
  }

  .footer .contact-and-social .contact-icon {
    width: 30px;
    height: 30px;
  }

  .header h1 {
    font-size: 24px;
  }

  .content {
    width: 100%;
    padding: 10px;
  }
}

/* Media Queries pentru telefoane foarte mici (sub 480px) */
@media (max-width: 480px) {
  .header h1 {
    font-size: 20px;
  }

  .social-links img {
    width: 60px;
    height: 60px;
  }

  .tabs button {
    width: 70px;
    height: 70px;
    margin: 5px auto;
  }

  .tabs button::after {
    font-size: 12px;
  }
}

@media (min-width: 1024px) {
  /* Stile pentru ecranele mari (desktop) */
  .main-content {
    display: flex;
    flex-direction: row; /* Menține layout-ul pe orizontală */
    padding: 50px 20px;
    width: 100%;
  }

  .tabs {
    flex-direction: column;
    width: 250px;
    height: auto;
    max-height: calc(100vh - 100px);
    position: fixed;
    left: 0;
    top: 100px;
    box-sizing: border-box;
  }

  .content {
    margin-left: 270px; /* Asigură că zona de conținut este la dreapta taburilor */
    padding: 20px;
    background: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow-y: auto;
    width: calc(100% - 270px); /* Lăsați spațiu pentru tab-uri */
  }
}